import React from 'react';
import PropTypes from 'prop-types';

import {makeStyles} from '@material-ui/core/styles';
import {AppBar, Toolbar, Button, Container, useScrollTrigger, IconButton, List, ListItem, ListItemText, Drawer} from '@material-ui/core';
import MenuIcon from '@material-ui/icons/Menu';
import {Link} from 'react-router-dom';

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        backgroundColor: "transparent",
        paddingBottom: theme.spacing(6),
    },
    appbar: {
        backgroundColor: theme.root.backgroundColor,
    },
    brand: {
        flexGrow: 1,
    },
    button: {
        border: '1px solid #00629B',
        color: '#00629B',
        marginLeft: theme.spacing(0),
        marginRight: theme.spacing(2),
    },
    navs: {
        [theme.breakpoints.down('sm')]: {
            display: 'none',
        },
    },
    nav: {
        textDecoration: 'none',
        color: '#00629B',
    },
    menuButton: {
        marginRight: theme.spacing(2),
        [theme.breakpoints.up('sm')]: {
          display: 'none',
        },
    },
}))

export default function Header(props) {
    const classes = useStyles()

    const [drawer, setDrawer] = React.useState(false)

    const navs = [
        {name: "Home", link: '/'},
        {name: "About Us", link: '/about'},
        {name: "Example2", link: '/'},
        {name: "Example3", link: '/'},
        {name: "Example4", link: '/'},
    ]

    function ElevationScroll(props) {
        const { children, window } = props;
        const trigger = useScrollTrigger({
            disableHysteresis: true,
            threshold: 0,
            target: window ? window() : undefined,
        });
    
        return React.cloneElement(children, {
            elevation: trigger ? 4 : 0,
        });
    }

    ElevationScroll.propTypes = {
        children: PropTypes.element.isRequired,
        window: PropTypes.func,
    };

    const Buttons = () => {
        let buttons = navs.map((nav) => {
            return (
                <Link to={nav.link}>
                    <Button color="inherit" className={classes.button}>{nav.name}</Button>
                </Link>
            )
        })
        return buttons
    }

    const handleDrawerToggle = (open) => (event) => {
        if (event && event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
            return;
        }
        setDrawer(open)
    }

    const list = () => (
        <div
          onClick={handleDrawerToggle(false)}
          onKeyDown={handleDrawerToggle(false)}
        >
          <List>
            {navs.map((nav) => (
              <ListItem button key={nav.name}>
                    <Link to={nav.link} className={classes.nav}>
                        <ListItemText primary={nav.name}/>
                    </Link>      
              </ListItem>
            ))}
          </List>
        </div>
    );

    return(
        <div className={classes.root}>
            <ElevationScroll {...props}>
                <AppBar position="fixed" className={classes.appbar}>
                    <Container>
                        <Toolbar>
                                <div className={classes.brand}>
                                    <img src='/assets/images/ieee_blue.jpg' height="40px" alt="IEEE logo"/>
                                </div>
                                <div className={classes.navs}>
                                    <Buttons button={classes.button}/>
                                </div>
                                <IconButton
                                    color="primary"
                                    onClick={handleDrawerToggle(true)}
                                    className={classes.menuButton}
                                >
                                    <MenuIcon />
                                </IconButton>
                        </Toolbar>
                    </Container>
                </AppBar>
            </ElevationScroll>
            <Drawer 
            anchor='right' 
            open={drawer} 
            onClose={handleDrawerToggle(false)}
            >
                {list()}
            </Drawer>  
        </div>
    )
}